/* =========== STARTbase remove kpbs stlye.css elements ===========*/
body {
  --topBarHeight: 0;
  --navBarHeight: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  color: #262D26;
  /* regular */
}
.container {
  max-width: 940px;
  margin: 0 auto;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  /* bold */
}
/* =========== END base remove kpbs stlye.css elements ===========*/
/* 
=====================================================================
button block
=====================================================================
*/
.arrow-btn {
  border-radius: 50px;
  background-clip: padding-box;
  font-size: 1.125em;
  display: inline-block;
  font-weight: 500;
  font-size: 16px;
  padding: 10px 25px;
  border: 1px solid #2638C4;
  background-color: #fff;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease 0s;
  white-space: nowrap;
}
.arrow-btn svg {
  position: relative;
  transition: right 0.2s ease 0s;
  right: 4px;
}
.arrow-btn:hover {
  box-shadow: 0px 15px 20px rgba(0, 25, 30, 0.4);
  color: #fff;
  transform: translateY(-7px);
}
.arrow-btn:hover svg {
  right: 0px;
  position: relative;
}
.arrow-btn svg {
  width: 20px;
  margin-left: 5px;
}
.arrow-btn.white-arrow-btn {
  background-color: #fff;
  color: #2638C4;
}
.arrow-btn.purple-arrow-btn {
  background-color: #2638C4;
  color: #fff;
  border: none;
}
.arrow-btn.purple-arrow-btn svg path {
  fill: #fff;
}
.arrow-btn.transparent-arrow-btn {
  background-color: transparent;
  color: #fff;
  border: 1px solid #fff;
}
.arrow-btn.transparent-arrow-btn svg path {
  fill: #fff;
}
/* 
=====================================================================
Hero Block
=====================================================================
*/
.passport-hero-banner {
  background-color: #0B135C;
  min-height: 440px;
  display: flex;
  flex-wrap: nowrap;
  color: #fff;
}
.passport-hero-banner__content {
  padding: 60px 0 60px 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.passport-hero-banner__logo img {
  margin-bottom: 25px;
}
.passport-hero-banner__cta {
  margin: 30px 0;
}
.passport-hero-banner p {
  margin: 0;
}
.passport-hero-banner__buttons {
  display: flex;
  flex-direction: row;
  gap: 15px;
}
.passport-hero-banner__image {
  width: 100%;
  background-position: initial;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(../img/passport_image.jpg);
  height: 440px;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.45);
}
.passport-hero-banner__image__circle {
  width: 400px;
  background: radial-gradient(55em 60em at -97% 50%, #0B135C 50%, transparent 50%);
  height: 440px;
}
@media (max-width: 768px) {
  .passport-hero-banner__image__circle {
    width: 300px;
    height: 440px;
  }
}
@media (max-width: 650px) {
  .passport-hero-banner {
    min-height: unset;
  }
  .passport-hero-banner__image {
    display: none;
  }
  .passport-hero-banner__content {
    padding: 40px 15px;
    text-align: center;
  }
  .passport-hero-banner__content img {
    margin: 0 auto;
  }
  .passport-hero-banner__content__cta {
    margin: 20px 0;
  }
}
/* 
=====================================================================
image-text__zigzag
=====================================================================
*/
.square-img-text {
  display: flex;
  justify-content: space-between;
  background-color: #FAFAFA;
}
.square-img-text__content {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.square-img-text__content__wrapper {
  max-width: 75%;
  display: flex;
  gap: 10px;
}
.square-img-text__content__wrapper--heading {
  display: flex;
  gap: 15px;
}
.square-img-text__content__wrapper--summary {
  font-size: 16px;
}
.square-img-text__content__wrapper--summary ul {
  padding: 15px 0;
}
.square-img-text__content__wrapper--summary ul li {
  margin-bottom: 15px;
  display: flex;
}
.square-img-text__content__wrapper--summary ul li svg {
  margin-right: 10px;
}
.square-img-text__content__wrapper--summary ul li:last-child {
  margin-bottom: 0;
}
.square-img-text__content__wrapper--summary p {
  margin: 16px 0;
  font-size: 16px;
}
.square-img-text__content__wrapper--summary-cta .arrow-btn {
  margin: 10px 0 0 0;
}
.square-img-text__content__wrapper--svg {
  padding-top: 5px;
}
.square-img-text__picture {
  width: 50%;
  position: relative;
}
.square-img-text__picture--svg svg {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
}
.square-img-text__picture--image {
  width: 100%;
  height: 400px;
}
.square-img-text__picture--image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.square-img-text.switch .square-img-text__content {
  order: 1;
}
.square-img-text.switch .square-img-text__picture {
  order: 2;
}
@media (max-width: 768px) {
  .square-img-text__content__wrapper {
    max-width: 100%;
    padding: 0 15px;
  }
}
@media (max-width: 650px) {
  .square-img-text__picture--image {
    width: 100% !important;
    height: unset !important;
  }
  .square-img-text__picture--image img {
    height: unset;
  }
}
@media (max-width: 600px) {
  .square-img-text {
    flex-direction: column;
  }
  .square-img-text__content {
    width: 100%;
  }
  .square-img-text__content__wrapper {
    max-width: 100%;
    padding: 40px 15px;
  }
  .square-img-text__picture {
    width: 100%;
  }
  .square-img-text.switch .square-img-text__content {
    order: 2;
  }
  .square-img-text.switch .square-img-text__picture {
    order: 1;
  }
}
/* 
=====================================================================
Table Membership tier
=====================================================================
*/
.passport-table {
  margin: 60px 0;
}
.passport-table__seperator {
  display: none;
  width: 1px;
  height: 20px;
  background: #e8eaed;
}
.passport-table__head {
  align-items: center;
  text-align: center;
  gap: 40px;
  justify-content: space-between;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.passport-table__head__col {
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.passport-table__head__col--content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.passport-table__head__col--content img {
  margin-bottom: 15px;
}
.passport-table__head__col--content--cta a {
  background-color: #2638C4;
  color: #fff;
  font-size: 12px;
  border-radius: 50;
  margin-bottom: 15px;
  white-space: unset;
}
.passport-table__head__col--content span {
  font-size: 14px;
  font-weight: bold;
  display: block;
}
.passport-table__head__col:nth-child(1) h3 {
  color: #2638C4;
  margin-bottom: 15px;
}
.passport-table__head__col:nth-child(2) h4 {
  font-size: 14px;
  max-width: 75%;
}
.passport-table__head__col:nth-child(3) h4 {
  font-size: 14px;
  max-width: 75%;
}
.passport-table__head__col:nth-child(1) {
  flex: 2;
}
.passport-table__head__col__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.passport-table__feature__item {
  border-bottom: 1px solid rgba(200, 200, 200, 0.4);
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 15px 0;
}
.passport-table__feature__item__bullet img {
  margin: 0 auto;
}
.passport-table__feature__item__bullet--new {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  grid-gap: 5px;
}
.passport-table__feature__item__bullet--new img {
  display: block;
  margin-left: 0;
}
.passport-table__feature__item__bullet:nth-child(1) {
  font-size: 14px;
  font-weight: 700;
}
.passport-table__feature__item__bullet:nth-child(2) {
  padding: 20px 0;
}
.passport-table__feature__item__bullet__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  text-align: center;
  justify-content: center;
  align-items: center;
}
.passport-table__feature__item.new-label .passport-table__feature__item__bullet--new img {
  display: inline-block;
}
.passport-table__feature__item:nth-child(1) {
  border-top: 1px solid rgba(200, 200, 200, 0.4);
  margin-top: 20px;
}
.passport-table__footer {
  display: flex;
  justify-content: center;
  align-items: center;
}
.passport-table__footer__buttons {
  display: flex;
  flex-direction: row;
  gap: 30px;
}
@media (max-width: 900px) {
  .passport-table {
    padding: 40px 15px;
    margin: 0;
  }
  .passport-table__head {
    gap: 40px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
  }
  .passport-table__head__col--content--cta {
    flex-direction: column;
    display: flex;
  }
  .passport-table__head__col__wrapper {
    display: flex;
    flex-direction: row;
    border-top: 1px solid rgba(200, 200, 200, 0.4);
  }
  .passport-table__head__col__wrapper > div {
    flex: 1;
    padding: 10px 5px;
  }
  .passport-table__head__col:nth-child(1) {
    flex: 1;
  }
  .passport-table__feature__item {
    border-bottom: 1px solid rgba(200, 200, 200, 0.4);
    display: flex;
    padding: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .passport-table__feature-item--text {
    text-align: center;
  }
  .passport-table__feature__item__bullet__container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    text-align: center;
    justify-content: center;
    align-items: center;
    width: 80%;
    padding: 7px 10px 17px;
  }
  .passport-table__seperator {
    display: block;
  }
  .passport-table__seperator {
    display: block;
    margin: 0 auto;
  }
  .passport-table__head__col__wrapper > div:nth-child(2) {
    margin-left: 20px;
    padding-left: 20px;
    border-left: 1px solid rgba(200, 200, 200, 0.4);
  }
  .passport-table__feature__item:nth-child(1) {
    margin-top: 0px;
  }
  .passport-table__feature__item__bullet--new {
    padding: 13px 0 0;
  }
}
@media (max-width: 768px) {
  .passport-table__head {
    gap: 30px;
  }
  .passport-table__footer {
    text-align: center;
  }
  .passport-table__footer__buttons {
    margin: 30px 0 0 0;
    gap: 30px !important;
  }
  .passport-table__footer__buttons > div {
    gap: 30px;
  }
  .passport-table__footer__buttons > div a {
    margin-top: 0px;
  }
  .passport-table__footer__buttons > div a:last-child {
    margin-bottom: 0;
  }
}
