    gap: 0;
  }
  .passport-table__footer__buttons a {
    margin: 15px 0 15px 0;
  }
}
@media (max-width: 450px) {
  .pp-footer__wrapper__poster {
    padding: 40px 15px;
  }
  .pp-footer__wrapper__poster--content--icons--row {
